ch excludes acts such as discrimination or racism, physical aggression or psychological aggression, acts of sexual aggression or inappropriate sexual advances, violation of privacy, immoral behavior or any behavior that may affect stability. emotion of a participant of the group of friends. These general rules must be followed by all those who belong to the group of friends, including administrators, MOTO-YA and other participating members. ")]),_c('h6',[_vm._v("1.3 People under the age of 18:")]),_c('p',[_vm._v(" You must be 18 years or older to be and require services within a friend group (WhatsApp or Facebook Messenger). This means that you must be at least 18 years old to ride unaccompanied in a vehicle. Account holders can't request a ride or delivery for someone under the age of 18 who will not be accompanied by either the account holder or another adult during the ride or when collecting the delivery. ")]),_c('h6',[_vm._v("1.4 Extra riders")]),_c('p',[_vm._v(" When driving with Moto-Ya, no one other than the driver, the requesting rider, and the rider’s guests should be in the vehicle. These rules apply unless our local rules, terms, or other policies say otherwise. When riding with Moto-Ya, the USER who requires the service in the group (WhatsApp or Facebook Messenger) is responsible for the behavior of their entire party. If you request a ride or a delivery for another adult, you’re held responsible for their behavior during their trip or delivery. ")]),_c('h6',[_vm._v("1.5 Vehicle Information (Car,Taxi,Bike,SUV…)")]),_c('p',[_vm._v(" When a driver takes a service from a group of friends, he must contact and provide the user requesting the service with information about his vehicle and important information for the user such as: plates, type of vehicle, color, name of the driver and approximate time of arrival. This in order to give confidence to users and provide transparency in the provision of services. "),_c('br'),_vm._v(" Drivers must complete trips and deliveries using only approved vehicles. To provide accurate information, let us know your vehicle information and provide any updates to your documentation that may become invalid, like a driver’s license that’s about to expire. ")]),_c('h6',[_vm._v("1.6 Helmets for Bikes, motorcycles and Scooters")]),_c('p',[_vm._v(" The drivers of these vehicles are recommended to use appropriate helmets corresponding to what the law requires, and they are also required to use helmets with accepted quality standards at the time a passenger transport service is provided in this type of vehicle. Before providing the passenger transport service by these drivers, the user must know and accept that they will be picked up in a vehicle such as: Motorcycle, bike or similar. ")]),_c('h6',[_vm._v("1.7 Use of Dashcams by Drivers")]),_c('p',[_vm._v(" Drivers may choose to install and use a dashcam, which can be used to record rides and provide evidence to Moto-Ya, law enforcement, or insurance companies in the event that something goes wrong on a ride. Please note the following: ")]),_c('p',[_vm._v(" Riders entering a rideshare vehicle with a dashcam may be concerned about how the video, their image, or conversations captured by a dashcam will be used. In some locations, local laws and regulations require that a rider provide consent to being recorded. Please check your local laws to understand your responsibilities. "),_c('br'),_vm._v(" Drivers may submit recordings to MOTO-YA at their discretion. MOTO-YA will review submitted footage and take all action consistent with terms of use. "),_c('br'),_vm._v(" Sharing or streaming a person’s image or audio or video recording on social media or in other digital or physical public locations is a violation of our RULES and may prompt further investigation by our safety team and even and even reach a civil or criminal proceeding. ")]),_c('h6',[_vm._v("1.8 Proper Vehicle Care")]),_c('p',[_vm._v(" Drivers affiliated with Moto-Ya are expected to maintain their vehicle with the required traffic standards and vehicle inspections according to what is stipulated by the insurance or car sales companies, for example, brakes, seat belt, oil, tires or any part that may affect the proper operation of the vehicle or that may affect the physical safety of a user. ")]),_c('h6',[_vm._v("1.9 Food Handling")]),_c('p',[_vm._v(" Users requesting food delivery services are expected to handle food under established food quality and handling standards. To provide a better and healthier service to the end user, merchants are expected to seal their packages and thus reduce costs. the risk of any virus, bacteria or contagion that can spread in them. The Drivers should deliver orders safely and in accordance with relevant safety standards. For example, they may not tamper with or open packaging during delivery. ")]),_c('h6',[_vm._v("2.0 Alcohol Delivery")]),_c('p',[_vm._v(" All orders that contain alcohol must comply with any applicable laws and restrictions on off-premises alcohol delivery and hours of service. Only users who are the legal age to buy alcohol (19 years old CANADA), and not intoxicated, can order and receive alcohol deliveries where such deliveries are available. The users will be asked to show government-issued photo IDs to verify age and identity when an alcohol delivery arrives. If the user does not have a valid government-issued photo ID, or appears intoxicated, the alcohol portion of the order will not be able to be delivered and may be returned to the seller, the price of the delivery must be paid by the user in the same way. "),_c('br'),_vm._v(" The users may not place an order that includes alcohol to be delivered to a public location where open containers are prohibited, or to any other areas where possession or consumption of alcohol is prohibited by law. ")]),_c('h6',[_vm._v("2.1 Prohibited Articles")]),_c('p',[_vm._v(" Users cannot send any article, food or goods prohibited by law or that may affect the safety of the driver. In the event that a user sends or attempts to send a prohibited item, the driver may deny the service or cancel it, in addition to notifying the administrators of the group of friends, this user can be suspended or permanently removed from the group of friends if so, stipulated by MOTO-YA. ")]),_c('h6',[_vm._v("2.2 Car Seats")]),_c('p',[_vm._v(" Children aged 12 and under should travel in the back seat. "),_c('br'),_vm._v(" When picking up riders traveling with small children, drivers can give them extra time to properly install a car seat before driving off. If a rider doesn’t have the appropriate car seat, if a driver is not comfortable with a rider installing one in their car, if a child does not appear to be within the height and weight safety requirements for the car seat, or if a driver otherwise feels that the child cannot be safely transported in the seat, the driver can cancel the ride. ")]),_c('h6',[_vm._v("2.3 Service Animal")]),_c('p',[_vm._v(" Drivers may not deny a ride to a rider with a service animal or assistive devices (such as a wheelchair or crutches) because of the service animal or assistive devices. Applicable laws generally prohibit drivers from denying service to riders because of their service animals or assistive devices, and from otherwise discriminating against riders with service animals or assistive devices. Knowingly refusing a rider a trip because of their service animal or assistive devices will result in losing access to the group of friends. "),_c('br'),_vm._v(" In Canada, a driver can request documentation from a rider if a service animal is not easily identifiable as one. ")]),_c('h6',[_vm._v("2.4 Fraud")]),_c('p',[_vm._v(" Intentionally falsifying information or assuming someone else’s identity for example by having a number in order to hinder the service deliberately increasing the delivery distance or a trip for fraudulent purposes as well as not providing real personal information at a checkpoint. Acceptin